import React from 'react';

import { ServicesManager } from 'services'
import { dateToString, durationToString } from 'time';

import { Table, TableProps } from 'components/Table'
import { PaginatorProps, Pagniator } from 'components/Paginator';
import { HistoryItem } from 'interfaces/frontend';

export type SessionListProps = {
  servicesManager: ServicesManager,
  gotoPageView: (pageIdx: number) => void,
  gotoItemView: (itemIdx: number) => void
  getPageItems: (pageIdx: number) => HistoryItem[]
  pageIdx: number
  maxPages: number
  isExhausted: boolean
}

type HistoryState =  {
  startIdx: number,
  maxItems: number,
  isFinished: boolean,
}


export class SessionList extends React.Component<SessionListProps, HistoryState> {
  render(): JSX.Element {
    // properties used by table
    const tableProps: TableProps<HistoryItem> = {
      headers: ['Prospect', 'Time', 'Duration', 'Disposition'],
      keyFunc: (item: HistoryItem): string => item.session_id,
      columnFuncs: [
        (item: HistoryItem) => item.person_name,
        (item: HistoryItem) => dateToString(item.scheduled_start),
        (item: HistoryItem) => durationToString(item.start == null || item.end == null ? 0 : (item.end.getTime() - item.start.getTime())),
        (item: HistoryItem) => item.dispositions ?? '',
      ],
      items: this.props.getPageItems(this.props.pageIdx),
      onRowClick: (item: HistoryItem): void => this.props.gotoItemView(item.itemIdx),
    }

    // properties used by paginator
    const paginatorProps: PaginatorProps = {
      currentPageIdx: this.props.pageIdx,
      numKnownPages: this.props.maxPages,
      mightHaveMorePages: !this.props.isExhausted,
      onSetPage: this.props.gotoPageView
    }

    return (
      <div className='flex flex-col items-start'>
        <div className='flex flex-col items-end'>
          <Table {...tableProps}></Table>
          <div className='p-4'></div>
          <Pagniator {...paginatorProps}></Pagniator>
        </div> 
      </div>
    )
  }
}