import { SERVICES_HOSTNAME, LIST_USER_SESSIONS_ENDPOINT, GET_SESSION_DATA_ENDPOINT, MESSAGE_TYPES, GET_SUMMARY_DATA_ENDPOINT } from "cfg/endpoints"
import { logInfo } from "core"
import { jsonSafeToSessionDataResult, jsonSafeToSummaryDataResult, jsonSafeToUserSessionResult, SessionDataResult, SummaryDataResult, UserSessionsResult } from "interfaces/services"
import { simpleGet } from "network"

const _LOG_SCOPE = `[Trellus Web][Services]`

export class ServicesManager {
  apiKey: string | null
  hostname: string
  mock: boolean

  constructor() {
    // initialize the api key from search params
    this.apiKey = (new URLSearchParams(window.location.search)).get('apiKey')
    this.hostname = (new URLSearchParams(window.location.search)).get('forceServicesHostname') ?? SERVICES_HOSTNAME
    this.mock = (new URLSearchParams(window.location.search)).get('mock') === 'true'

    // and overwrite on message
    window.addEventListener('message', this.handleMessage.bind(this))
    logInfo(`${_LOG_SCOPE} Set up service manager`)
  }

  handleMessage(event: MessageEvent) {
    switch (event.data['type']) {
      case MESSAGE_TYPES.APP_TO_EXTERNAL_SET_EXTENSION_INFO:
        logInfo(`${_LOG_SCOPE} Setting extension info`)
        this.apiKey = event.data['apiKey'] ?? this.apiKey
        this.hostname = event.data['forceServicesHostname'] ?? this.hostname
        break
      case MESSAGE_TYPES.APP_TO_EXTERNAL_CHECK_IS_LOADED:
        logInfo(`${_LOG_SCOPE} Responding to check`)
        window.postMessage({type: MESSAGE_TYPES.EXTERNAL_TO_APP_IS_LOADED})
        break
      case MESSAGE_TYPES.EXTERNAL_TO_APP_IS_LOADED:
        break
    }
  }

  async getApiKey(): Promise<string> {
    if (this.apiKey == null)
      throw new Error() // todo: wait to see if it comes in
    return this.apiKey
  }

  async getHostname(): Promise<string> {
    if (this.hostname == null) 
      throw new Error() // todo: wait to see if it comes in
    return this.hostname
  }


  async getSummaryData(): Promise<SummaryDataResult> {
    if (this.mock) return mockSummaryData()
    
    // formulate request
    const apiKey = await this.getApiKey()
    const hostname = await this.getHostname()
    const url = `https://${hostname}/${GET_SUMMARY_DATA_ENDPOINT}`
    const parameters: Record<string, string> = {'api_key': apiKey}

    // do it
    const result = await simpleGet(url, parameters)
    return jsonSafeToSummaryDataResult(result)
  }

  async getListUserSessions (maxStart: number | null = null): Promise<UserSessionsResult> {
    // formulate request
    const apiKey = await this.getApiKey()
    const hostname = await this.getHostname()
    const url = `https://${hostname}/${LIST_USER_SESSIONS_ENDPOINT}`
    const parameters: Record<string, string> = {'api_key': apiKey}
    if (maxStart != null) parameters['max_start'] = `${maxStart}`

    // do it
    const result = await simpleGet(url, parameters)
    return jsonSafeToUserSessionResult(result)
  }

  async getSessionData(sessionId: string): Promise<SessionDataResult> {
    // formulate request
    const apiKey = await this.getApiKey()
    const hostname = await this.getHostname()
    const url = `https://${hostname}/${GET_SESSION_DATA_ENDPOINT}`
    const parameters: Record<string, string> = {'api_key': apiKey, 'session_id': sessionId}
    
    // do it
    const result = await simpleGet(url, parameters)
    return jsonSafeToSessionDataResult(result)
  }

}

