import React from 'react';

import { ServicesManager } from 'services'
import { HistoryItem } from 'interfaces/frontend';

import { SessionList, SessionListProps } from 'components/SessionList'
import { SessionDetails, SessionDetailsProps } from 'components/SessionDetails';

type HistoryState = {
  viewingItemIdx: number // both items and pages are zero indexed
  numItems: number
  itemsPerPage: number
  isExhausted: boolean
  viewingItem: boolean
}

export class History extends React.Component<{servicesManager: ServicesManager}, HistoryState> {
  _items: HistoryItem[]
  _isExtending: boolean
  _maxStartJsonSafe: number | null
  _gotoListViewBound: (pageIdx?: number) => void
  _gotoItemViewBound: (itemIdx: number) => void
  _getPageItemsBound: (pageIdx: number) => HistoryItem[]
  _getItemBound: (itemIdx: number) => HistoryItem
  
  constructor(props: {servicesManager: ServicesManager}) {
    super(props)
    this.state = {viewingItemIdx: 0, numItems: 0, itemsPerPage: 20, isExhausted: false, viewingItem: false}
    this._items = []
    this._isExtending = false
    this._maxStartJsonSafe = null
    this._gotoListViewBound = this._gotoListView.bind(this)
    this._gotoItemViewBound = this._gotoItemView.bind(this)
    this._getPageItemsBound = this._getPage.bind(this)
    this._getItemBound = this._getItem.bind(this)
  }

  /**
   * Asynchronously extend the known conversations.
   * This function has concurrency guards and only one instance can be running at any time
   */
  async _extendConversations(): Promise<undefined> {
    // exit early if all items have been fetched or there is an ongoing extension
    if (this.state.isExhausted || this._isExtending) return
    this._isExtending = true
    
    // get new items
    const result = await this.props.servicesManager.getListUserSessions(this._maxStartJsonSafe)
    result.items.forEach((x, i) => this._items.push({...x, itemIdx: this._items.length}))
    this._maxStartJsonSafe = result.maxStartJsonSafe

    // update state
    this.setState((state, _props) => {
      this._isExtending = false
      return {
        numItems: state.numItems + result.items.length,
        isExhausted: state.isExhausted || result.items.length === 0,
      }
    })
  }

  /**
   * Extend conversations if the user is near viewing the end
   */
  _maybeExtendConversations() {
    if (this._items.length <= this.state.viewingItemIdx + this.state.itemsPerPage)
      this._extendConversations()
  }

  /**
   * Switch the history viewer to a list view of the specified page if provided
   * If a page index is not provided, the view will correspond to the page of the last viewed item
   * @param pageIdx Page index to view
   */
  _gotoListView(pageIdx?: number): void {
    if (pageIdx == null) 
      this.setState({viewingItem: false})
    else
      this.setState((state, _props) => ({viewingItemIdx: pageIdx * state.itemsPerPage, viewingItem: false}))
    
  }

  /**
   * Switch the history viewer to a item view of the specified item
   * @param itemIdx Item index to view
   */
  _gotoItemView(itemIdx: number) {
    this.setState({viewingItemIdx: itemIdx, viewingItem: true})
  }

  /**
   * Return items from the specified page
   * @param pageIdx Page index to view
   */
  _getPage(pageIdx: number): HistoryItem[] {
    return this._items.slice(pageIdx * this.state.itemsPerPage, (pageIdx + 1) * this.state.itemsPerPage)
  }

  /**
   * Return the item from the specified index
   * @param itemIdx Item index to view
   */
  _getItem(itemIdx: number): HistoryItem {
    return this._items[itemIdx]
  }

  /**
   * Start loading data on mount
   */
  componentDidMount(): void {
    this._extendConversations()
  }

  /**
   * After state changes try to update conversations
   */
  componentDidUpdate(): void {
    this._maybeExtendConversations()
  }

  /**
   * Render either a list view or a details view
   */
  render(): JSX.Element {
    if (this.state.viewingItem) {
      const sessionDetailsProps: SessionDetailsProps = {
        servicesManager: this.props.servicesManager,
        gotoListView: this._gotoListViewBound,
        gotoItem: this._gotoItemViewBound,
        getItem: this._getItemBound,
        itemIdx: this.state.viewingItemIdx,
        numItems: this.state.numItems,
        isExhausted: this.state.isExhausted,
      } 
      return <SessionDetails {...sessionDetailsProps}></SessionDetails>
    } else {
      const sessionListProps: SessionListProps = {
        servicesManager: this.props.servicesManager,
        gotoPageView: this._gotoListViewBound,
        gotoItemView: this._gotoItemViewBound,
        getPageItems: this._getPageItemsBound,
        pageIdx: Math.floor(this.state.viewingItemIdx / this.state.itemsPerPage),
        maxPages: Math.ceil(this.state.numItems / this.state.itemsPerPage),
        isExhausted: this.state.isExhausted
      }
      return <SessionList {...sessionListProps}></SessionList>
    }
  }
}