import React from 'react'

import './App.css'

import { HOME_PAGE, Page } from 'cfg/pages'
import { ServicesManager } from 'services'
import { AppUser } from 'interfaces/frontend'

import { History } from 'components/History'
import { Home } from 'components/Home'
import { SideBar } from 'components/SideBar'


export class App extends React.Component<{}, {user: AppUser, page: Page}> {
  servicesManager: ServicesManager
  _setPageBound: (page: Page) => void
  
  constructor(props: {}) {
    super(props)
    const apiKey = (new URLSearchParams(window.location.search)).get('apiKey')
    this.state = {user: {apiKey: apiKey, name: null}, page: HOME_PAGE}
    this.servicesManager = new ServicesManager()
    this._setPageBound = this._setPage.bind(this)
  }

  _setPage(page: Page): void {
    this.setState({page: page})
  }

  _getContentNode(): JSX.Element {
    switch (this.state.page.kind) {
      case 'history':
        return <History servicesManager={this.servicesManager}/>
      case 'home':
        return <Home servicesManager={this.servicesManager}/>
      default:
        const _exhaustive: never = this.state.page.kind
        return _exhaustive
    }
  }
  
  render(): JSX.Element {
    return (
      <div className='flex select-none'>
        <SideBar page={this.state.page} setPage={this._setPageBound}/>
        <div className='grow h-screen overflow-auto p-4 white text-slate-800'>
          {this._getContentNode()}
        </div>
      </div>
    )
  }
}
