import React from "react";


export type TableProps<ItemType> = {
  headers: string[],
  keyFunc: (item: ItemType) => string,
  columnFuncs: ((item: ItemType) => string | JSX.Element)[],
  items: ItemType[],
  onRowClick?: (item: ItemType) => void,
}

export class Table<ItemType> extends React.Component<TableProps<ItemType>, {}> {
  _keyToItem: Map<string, ItemType>
  _handleClickBound: (event: React.MouseEvent<HTMLElement>) => void

  constructor(props: TableProps<ItemType>) {
    super(props)
    this._keyToItem = new Map(this.props.items.map((x) => [this.props.keyFunc(x), x]))
    this._handleClickBound = this._handleClick.bind(this)
  }

  _handleClick (event: React.MouseEvent<HTMLElement>) {
    const onRowClick = this.props.onRowClick
    if (onRowClick == null) return
    const target = event.target
    if (!(target instanceof HTMLElement) || target.tagName !== 'tr') return
    const targetKey = target.getAttribute('key')
    if (targetKey == null) return
    const item = this._keyToItem.get(targetKey)
    if (item == null) return
    onRowClick(item)
    event.stopPropagation()
  }
  
  render(): JSX.Element {
    // make rows
    const trClassName = "odd:bg-slate-50 even:bg-slate-100 cursor-pointer border-b border-slate-300 hover:bg-slate-200"
    const tdClassName = 'py-3 px-6 text-center'
    const rowElements = this.props.items.map((x, i) => {
      const onRowClick = this.props.onRowClick
      const onClick = onRowClick == null ? undefined : () => onRowClick(x)
      return (
        <tr key={this.props.keyFunc == null ? i : this.props.keyFunc(x)} className={trClassName} onClick={onClick}>
          {this.props.columnFuncs.map((y, j) => <td className={tdClassName} key={j}>{y(x)}</td>)}
        </tr>)
    })

    return (
      <table className="min-w-max drop-shadow-md">
        <thead>
          <tr className="bg-slate-200 uppercase text-sm">
            {this.props.headers.map((x, i) => <th className='py-3 px-6 text-center' key={i}>{x}</th>)}
          </tr>
        </thead>
        
        <tbody className={this.props.onRowClick == null ? "" : 'cursor-pointer'} onClick={this.props.onRowClick == null ? undefined : this._handleClickBound}>
          {rowElements}
        </tbody>
      </table>
    )
  }
}